<template lang="pug">
	.auth
		.header-auth
			img.photo(:src="user.photo")
			span.title {{isAuth ? user.name : 'Minhas mensagens'}}

		.wrap
			.no-auth(v-if="!isAuth")
				input(placeholder="email picpay", v-model="email", autofocus)
				button.button-submit(
					@click="auth",
					:class="email.length >= 4 ? '' : 'disable'") Entrar

			.on-auth(v-else)
				.wrap-input
				.list
					.message(v-for="message in user.messages", :class="['theme-'+message.configs.theme, 'align-'+message.configs.align, 'font-'+message.configs.font]")
						.borders
							.top
							.bottom

						.text {{message.text}}
</template>

<script>
// @ is an alias to /src
import { collection, query, where, getFirestore, getDocs, doc, getDoc } from "firebase/firestore";
import cookies from 'js-cookie'

export default {
	name: 'HomeView',
	data (){
		return{
			email: '',
			isAuth: false,
			user: {}
		}
	},
	components: {
	},
	created(){
		this.auth();
	},
	async beforeRouteEnter(to, from, next) {
		if(cookies.get('user')){
			next(vm => {
				vm.fetchAuth(cookies.get('user'))
				vm.isAuth = true;
			});
		}else{
			next(vm => {
				vm.isAuth = false;
			});
		}
	},
	mounted() {
		this.$root.$el.classList.remove('openMenu')
	},
	methods: {
		async fetchAuth(id){
			const db = getFirestore();
			const snap = await getDoc(doc(db, 'users', id))

			if (snap.exists()) {
				this.user = snap.data();
				this.isAuth = true;
			}
			else {
				console.log("No such document")
			}
		},
		setUserDetails(data){
			console.log(data)
		},
		async auth(){
			const db = getFirestore();
			const usersRef = collection(db, 'users');
			// Create a query against the collection.
			const q = query(usersRef, where('email', '==', this.email));

			let querySnapshot = await getDocs(q);
			querySnapshot.forEach((doc) => {
				cookies.set('user', doc.id)
			});

			this.fetchAuth(cookies.get('user'));
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Auth.styl"></style>
